import { Component, Input, OnInit } from '@angular/core';
import { IUnit } from 'src/app/models/IUnit';

@Component({
  selector: 'app-list-weight',
  templateUrl: './list-weight.component.html',
  styleUrls: ['./list-weight.component.scss'],
})
export class ListWeightComponent implements OnInit {
  @Input() units: IUnit[];

  constructor() { }

  ngOnInit() {}

  avatar(state){
    switch (state) {
      case 0:
        return "../../../assets/check-ok.svg"
        break;
      case 1:
        return "../../../assets/arrow-up.svg"
        break;
      case 2:
        return "../../../assets/arrow-down.svg"
        break;
      case 3:
        return "../../../assets/circle.svg"
        break;
      default:
        break;
    }
  }

}
