import { ChangeDetectorRef, Component, Input, OnInit, SimpleChanges } from '@angular/core';
import { IBatch } from 'src/app/models/IBatch';
import { IPO } from 'src/app/models/IPO';
import { IProduct } from 'src/app/models/IProduct';
import { MachineState } from './../../models/EMachineState';

@Component({
  selector: 'app-status-bar',
  templateUrl: './status-bar.component.html',
  styleUrls: ['./status-bar.component.scss'],
})

export class StatusBarComponent implements OnInit {
  @Input()  po: IPO;
  @Input() product: IProduct;
  @Input() batch: IBatch;
  @Input() state: MachineState;
 
  init: boolean = false;
  machine_state: string = "";
  speed: string = '45';

  constructor(private cd: ChangeDetectorRef) { }

  ngOnInit() {
    this.init = true;
    this.setTextState();
    console.log(this.state)
  }

  ngAfterViewInit() {
    this.cd.detectChanges();
  }

  ngOnChanges(changes: SimpleChanges) {
    if (this.init) {
      this.setTextState();
    }
  }

  setTextState(){
    if (this.init) {
      switch (this.state) {
        case 0:
          this.machine_state = 'INICIANDO SISTEMA';
          break;
        case 1:
          this.machine_state = 'CONFIGURACION';
          break;   
        case 2:
          this.machine_state = 'DETENIDA';
          break; 
        case 3:
          this.machine_state = 'LISTA';
          break; 
        case 4:
          this.machine_state = 'EN MARCHA';
          break; 
        case 5:
          this.machine_state = 'ESPERA';
          break; 
        default:
          break;
      }
      console.log(this.machine_state);
    }
  }

}
