import { Component, Input, OnInit } from '@angular/core';
import { IWeightLimits } from 'src/app/models/IWeightLimits';

@Component({
  selector: 'app-weight-circle',
  templateUrl: './weight-circle.component.html',
  styleUrls: ['./weight-circle.component.scss'],
})
export class WeightCircleComponent implements OnInit {

  @Input() weight: number = 0;
  @Input() result: number = 0;
  @Input() limits: IWeightLimits = {max: 0, min: 0};

  titleFontSize: string = "16";
  subtitleFontSize: string = "10";
  constructor() { }

  ngOnInit() {}

  formatSubtitle(percent: number) : string {
    if(percent >= 100){
      return "Sobrepeso"
    }else if(percent <= 7.8 && percent >= 6.4){
      return "Aceptado"
    }else if(percent < 6.4){
      return "Peso bajo"
    }else{
      return '--'
    }
  }

  color(): string{
    switch (this.result) {
      case 0:
        return '#33FF3C'
        break;
      default:
        return '#FF0F0F'
        break;
    }
  }
}
